}
.widget_calendar .calendar_wrap td a {
    font-weight: 500;
}
.widget_calendar td,
.widget_calendar th {
    padding: 0.4em;
}
.widget_calendar caption {
    text-align: center;
    border-bottom: 1px solid;
    font-weight: 600;
}
.calendar_wrap th ,tfoot td { 
	background:#ff5e14;
}
.widget_calendar caption {
	border-color: #ff5e14;
}
/*widget tags-list*/
.widget_tag_cloud a {
    padding: 8px 10px;
    background-color: #fff;
    font-size: 12px;
    display: inline-block;
    margin: 0 5px 5px 0;
    color: #020d26;
    border: 0px solid rgba(0,0,0,0.1);
    font-weight: 600;
    border-radius: 0;
    text-transform: uppercase;
    box-shadow: 0 5px 10px 0 rgba(0,0,0,0.15);
}
.widget_tag_cloud a:hover {
    background-color:var(--primary);
	color:#fff;
	
}
.widget_tag_cloud.radius a{
	border-radius:40px;
	padding: 7px 10px;
}
/*widget archive*/
.widget_archive select {
    width: 100%;
    padding: 5px;
    border: 1px solid #CCC;
}
/*widget text*/
.widget_text select {
    width: 100%;
    padding: 5px;
    border: 1px solid #CCC;
}
.widget_text select option {
    width: 100%;
}
/*widget categories*/
.widget_categories li {
    text-align: right;
}
.widget_categories li a {
    float: left;
	text-transform: capitalize;
}
.widget_rss ul {
    margin: 0;
    line-height: 20px;
}
.widget_rss ul li {
    line-height: 20px;
    margin-bottom: 15px;
}
.widget_rss ul .rsswidget {
    color: #000;
}
.widget_rss ul .rss-date {
    color: #909090;
	font-size: 13px;
	font-weight: 400;
}
.widget_rss ul .rssSummary {
    padding: 5px 0;
}
.widget_rss ul cite {
    color: #333;
    font-weight: 500;
}
/*widget rss*/
.rsswidget img {
    display: inherit;
}
a.rsswidget,
cite{
    font-weight: 500;
}
.rssSummary {
    margin: 10px 0;
	line-height: 24px;
	font-size: 14px;
}
.rss-date{
    font-style: italic;
}
.widget_rss ul li {
    margin-bottom: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid rgba(120,120,120,0.5);
    padding-top: 0;
}
.widget_rss ul li:last-child {
    margin-bottom: 0;
    border-bottom: 0;
    padding-bottom: 0;
}
/* widget get in tuch */
.widget_getintuch li {
    margin-bottom: 18px;
    position: relative;
    padding-left: 40px;
}
.widget_getintuch b,
.widget_getintuch strong {
    display: block;
    text-transform: uppercase;
}
.widget_getintuch i {
	position: absolute;
	left: 0;
	top: 0px;
	text-align: center;
	font-size: 20px;
	width: 30px;
	height: 20px;
	line-height: 25px;
}
/* widget gallery */
.widget_gallery ul {
    padding-left: 0;
	display: table;
}
.widget_gallery.gallery-grid-4 li {
	width:25%;
}
.gallery-grid-8 li {
	width:12.5%;
}
@media only screen and (max-width: 767px) {
	.gallery-grid-8 li {
		width:25%;
	}
}
.widget_gallery li {
    display: inline-block;
    width: 33.33%;
	float:left;
	transition:all 0.5s;
	-webkit-transition:all 0.5s;
	-moz-transition:all 0.5s;
	-o-transition:all 0.5s;
	padding: 2px;
}
.widget_gallery li img {
    display: inline-block;
	width:100%;
}
.widget_gallery li:nth-child(3n-3) {
    margin-right: 0;
}
.widget_gallery li:hover {
	opacity:0.7;
	transition:all 0.5s;
	-webkit-transition:all 0.5s;
	-moz-transition:all 0.5s;
	-o-transition:all 0.5s;
}
.widget_gallery a {
    display: inline-block;
}
/* Newslatter */
.widget-newslatter .news-box{
	background-color:#F5F6F7;
	padding:30px;
	position: relative;
    z-index: 1;
	overflow: hidden;
}
.widget-newslatter p{
	font-style: italic;
	font-size:15px;
	margin-bottom: 15px;
}
.widget-newslatter .form-control {
    background: #ffffff none repeat scroll 0 0;
    border: 1px solid #d0d0d0;
    height: 40px;
    margin-bottom: 0;
    padding: 5px 15px;
}
.widget-newslatter .btn{
	padding:10px 15px;
	font-weight: 700;
	width:50px;
}
.news-box:before {
	font-family: themify;
	content: "\e75a";
	font-size: 80px;
	font-weight: 700;
	opacity: 0.05;
	position: absolute;
	right: 20px;
	top: -10px;
	z-index: -1;
	opacity: 0.05;
}
.news-box form{
	position:relative;
}
/* item-widgets-box */
.item-widgets-box .item-widgets-left{
    width: 120px;
    max-width: 120px;
