    overflow: hidden;	
}
.item-widgets-box .item-title{
	font-size: 18px;
    line-height: 24px;
    margin-bottom: 5px;
}
.item-widgets-box{
	display:flex;
	margin-bottom:15px;
}
.item-widgets-body{
	width:100%;
	max-width:100%;
	padding-left:20px;
}
.item-review{
	margin: 0 0 2px 0;
    list-style: none;
    padding: 0;
	
}
.item-review li{
	display: inline-block;
    color: #ffb805;
    font-size: 14px;
}
.item-review li a{
	
	
}
.item-widgets-box .item-widgets-body .price{
    font-size: 16px;
    font-weight: 500;
}
.item-widgets-box .item-widgets-body .price del{
	margin-left: 10px;
}

