	position:relative;
	z-index: 1;
}
.service-icon-bx .service-icon a{
	font-size: 150px;
    background: #e1e3ff;
    border-radius: 100%;
    height: 300px;
    width: 300px;
    display: inline-block;
    text-align: center;
    line-height: 300px;
	overflow:hidden;
}
.service-icon-bx .service-icon a img{
	height: 100%;
	vertical-align: top;
	object-fit: cover;
}
.service-icon-bx .service-info{
	padding: 0 0 0 50px;
	max-width: 400px;
    margin-left: auto;
}
.service-icon-bx:nth-child(2n){
	flex-direction: row-reverse;
}
.service-icon-bx:nth-child(2n) .service-info{
	padding: 0 50px 0 0;
}
.service-icon-bx:after{
    content: "";
    width: 110px;
    height: calc(100% + 4px);
    position: absolute;
    bottom: -2px;
    border-radius: 120px 0 0px 120px;
    border: 2px dashed rgba(0,0,0,0.2);
    left: -110px;
    border-width: 2px 0 2px 2px;
    z-index: -1;
}
.service-icon-bx:first-child:after{
	height: 220px;
    left: -110px;
    border-radius: 110px 0 0 110px;
    border-width: 2px 0px 2px 2px;
    right: auto;
}
.service-icon-bx:nth-child(2n):after{
	height: calc(100% + 4px);
    left: auto;
    border-radius: 0 110px 110px 0;
    border-width: 2px 2px 2px 0px;
    right: -110px;
}
.service-icon-bx:last-child:after{
    left: auto;
    right: -110px;
    bottom: auto;
    top: -2px;
    border-radius: 0 120px 120px 0;
    border-width: 2px 2px 2px 0px;
	height: 220px;	
}
.service-icon-bx:nth-child(2n) + .service-icon-bx:last-child:after{
	right: auto;
    left: -110px;
    bottom: auto;
    top: -2px;
    border-radius: 120px 0 0 120px;
    border-width: 2px 0px 2px 2px;
}
.service-icon-bx .service-icon{
	position:relative;
}
.service-icon-bx .service-icon:after{
	content: attr(data-name);
    font-size: 18px;
    font-weight: 700;
    position: absolute;
    line-height: 50px;
    top: 50%;
    right: -30px;
    width: 60px;
    transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    height: 60px;
    background: var(--primary);
    border-radius: 100%;
    text-align: center;
    color: #fff;
    border: 5px solid #fff;
}
.service-icon-bx:nth-child(2n) .service-icon:after{
	right: auto;
	left:-30px;
}
