    padding-left: 2px;
}
.service-list li a:after{
	content: "";
    height: 100%;
    background-color: var(--primary);
    width: 0;
    position: absolute;
    right: 0;
    transition: all 0.5s;
    -moz-transition: all 0.5s;
    -webkit-transition: all 0.5s;
    -ms-transition: all 0.5s;
    -o-transition: all 0.5s;
    z-index: -1;
    top: 0;
    transform-origin: right;
