}
.timeline-view:after {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 25%;
    margin-left: 0px;
    border-left: 2px dashed #ccc;
}
.timeline-area {
	position: relative;
	background-color: inherit;
	width: 100%;
	left: 0; 
	display: flex; 
}
.timeline-area:last-child .timeline-content{
	border:0;
