.video-zoom{
	position:relative;
}
.video-zoom:after,
.video-zoom:before{
	-webkit-animation:2s scrollup infinite linear;
	animation:2s scrollup infinite linear;
	content:"";
	width: 100%;
    position: absolute;
    height: 100%;
    border: 1px solid var(--sc-primary);
    left: 0;
    border-radius: 100px;
}

.video-zoom:before{
	-webkit-animation:2s scrollup1 infinite linear;
	animation:2s scrollup1 infinite linear;
}
@keyframes scrollup{
	0%{
		-webkit-transform:scale(1);
		        transform:scale(1);
		opacity:1;
	}
	100%{
		-webkit-transform:scale(1.7);
		        transform:scale(1.7);
		opacity:0
	}
}
@-webkit-keyframes scrollup1{
	0%{
		-webkit-transform:scale(1);
		        transform:scale(1);
		opacity:1;
	}
	100%{
		-webkit-transform:scale(1.35);
		        transform:scale(1.35);
		opacity:0
	}
}
@keyframes scrollup1{
	0%{
		-webkit-transform:scale(1);
		        transform:scale(1);
		opacity:1;
	}
	100%{
		-webkit-transform:scale(1.35);
		        transform:scale(1.35);
		opacity:0
	}
}
