    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    top: 40%;
    border: 10px solid #fff;
	box-shadow: 20px 20px 50px 0 rgba(0,0,0,0.1);	
}

/* list-info */
.list-circle li {
    display: block;
    position: relative;
    padding: 5px 5px 5px 30px;
    font-size: 16px;
}
.list-circle li:before {
    content: "\f10c";
}

/* portfolio-box */
.portfolio-box .magnific-anchor.right {
    width: auto;
    float: right;
    right: 0;
    padding-left: 30px;
    padding-right: 30px;
}

/* service-bx style1 */
.service-bx.style1{
	display: flex;
	box-shadow: unset;
	align-items: center;
	border: 1px solid rgba(0,0,0,0.1);
	border-radius: 10px;
}
.service-bx.style1 .service-media{
	width: 120px;
	border-radius: 10px;
	overflow: hidden;
}
.service-bx.style1 .service-info {
    padding: 10px 20px;
}
.service-bx.style1 .service-info p {
    margin-bottom: 10px;
}
.service-bx.style1 .service-info .btn-link{
	font-size: 14px;
}
.service-bx.style1 .service-info .title {
    font-size: 18px;
    line-height: 20px;
    margin-bottom: 5px;
}

/* history-box */
.history-box .nav-tabs {
    border-bottom: 2px solid rgba(255,255,255,0.3);
    justify-content: center;
	margin-bottom: 50px;
}
.history-box .nav-tabs li{
    color: #fff;
    font-size: 20px;
    font-weight: 500;
    padding: 15px 20px;
    display: block;
	text-transform: capitalize;
    margin-bottom: -1px;
	border-bottom: 2px solid transparent;
	border-width: 0 0 2px;
	background-color: transparent;
}
.history-box .nav-tabs li.react-tabs__tab--selected{
	box-shadow: none;
}
.history-box .nav-tabs li.react-tabs__tab--selected:after{
	content: none;
}
.history-box .nav-tabs li.react-tabs__tab--selected,
.history-box .nav-tabs li:hover{
    border-bottom: 2px solid #fff;
}
.history-box .history-info{
	padding: 50px 30px;
}
.history-box .tab-content .tab-pane img{
	width: 100%;
}

/* testimonial-bx style3 */
.testimonial-bx.style3,
.testimonial-bx.style4{
	background-color: transparent;
	padding: 0 0 40px;
}
.testimonial-bx.style3 .testimonial-content{
	padding-bottom: 30px;
}
