    max-width: 260px;
}


/* dots-style-2  */
.dots-style-2 .slick-dots {
    display: inline-block;
    margin: 0 5px;
}
.dots-style-2 .slick-dots li button:before{
	content: none;
}
.dots-style-2 .slick-dots li{
	width: auto;
}
