.ttr-tabs .nav-tabs > li > a i,
h1 a:hover,
h2 a:hover,
h3 a:hover,
h4 a:hover,
h5 a:hover,
h6 a:hover,
.btn.outline,
.btn-link:hover {
    color: #e94a00;
}


/*===================== 	Secondery bg color =====================*/

.bg-secondry {
    background-color: #2d3239;
}


/*===================== 	Secondery text color =====================*/

.text-secondry {
    color: #bbf059;
}

.ovpr-light:after,
.ovpr-middle:after,
.ovpr-dark:after,
.ovpr:after{
    background-color: #bbf059;
}

.ttr-notify-header::after,
.about-year,
.widget-bg1,
.ttr-search-bar,
.ttr-header,
.account-head:after {
    /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#424bff+0,fe5a0e+100 */
	background: #424bff; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
	background: linear-gradient(to bottom, #424bff 0%,#bbf059 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#424bff', endColorstr='#fe5a0e',GradientType=0 ); /* IE6-9 */
    /* IE6-9 fallback on horizontal gradient */
}


/* Secondry Color */
.btn:active,
.btn:focus,
.btn:hover{
    background-color: #bbf059;
}




.ovprd-light:after,
.ovprd-middle:after,
.ovprd-dark:after,
.ovprd:after{
	background-color:#000374;
}

[class*="ovbl-"] .counter-style-1 .counter-text,
.header-transparent .secondary-menu .btn-link:hover,
.header-transparent .menu-links .nav > li:hover > a,
.header-transparent .menu-links .nav > li.active > a{
    color: #bbf059;
}
button.back-to-top:hover{
    background-color: #bbf059;
    color: #fff;
}

.heading-bx.left .title-head {
    border-color: #bbf059;
}

.text-primary{
	color: #bbf059!important;
}
.bg-primary{
	background-color: #bbf059!important;
}
