}
.timeline-nav ul li a{
	display: block;
	width: 100%;
	font-size: 22px;
	font-weight: 700;
	color: var(--primary);
	opacity: 0.3;
	transition:all 0.5s;
	-moz-transition:all 0.5s;
	-webkit-transition:all 0.5s;
	-ms-transition:all 0.5s;
	-o-transition:all 0.5s;
	padding: 0 0;
	text-decoration:underline;
	padding: 5px 0;
}
.timeline-nav ul li a.active{
	opacity: 1;
	font-size: 44px;
	color: var(--primary);
