}
.timeline-box{
	margin-bottom:60px;
}
.timeline-box p{
    font-size: 18px;
    line-height: 32px;
    color: #404153;
}

/* Time Line 2 */
.timeline-view {
	position:relative;
    max-width: 1000px;
    margin: auto;	
}
.timeline-view:after {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 25%;
    margin-left: 0px;
    border-left: 2px dashed #ccc;
}
.timeline-area {
	position: relative;
	background-color: inherit;
	width: 100%;
	left: 0; 
	display: flex; 
}
.timeline-area:last-child .timeline-content{
	border:0;
	margin-bottom:0;

}
.timeline-content{
    max-width: 75%;
    flex: 0 0 75%;
    padding-left: 30px;
    border-bottom: 1px solid rgba(0,0,0,0.1);
    padding-bottom: 30px;
    margin-bottom: 50px;
}
.timeline-content p{
    font-size: 18px;
    color: #777;
    margin-bottom: 15px;
}
.timeline-date .date-bx{
    font-weight: 600;
    color: #000;
    font-size: 40px;
    line-height: 30px;
    padding: 10px 0 20px 0;
    border-radius: 4px;
    position: relative;
    width: 100%;
}
.timeline-date .date-bx:before {
	content: '';
    position: absolute;
    width: 18px;
    height: 18px;
    right: -35px;
    background-color: white;
    border: 5px solid var(--primary);
    top: 15px;
    border-radius: 50%;
    z-index: 1;
}
